:root {
  --color-black: #000;
  --color-white: #fff;
  --color-ocean-blue: #1e3747;
  --color-light-gray-blue: #72c1c7;
  --color-green-blue: #283949;
  --color-orange: #ff9027;
  --color-crayola: #e8ecf1;
  --color-lavender: #f8f9fd;
  --color-distant-blue: #4a6278;
  --color-dark-blue: #243d4d;
  --color-slate-gray: #2d4656;
}

@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothamPro-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothamPro-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothamPro-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Noah";
  src: url("../fonts/Noah-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

